export default getSvgHeader;

function getSvgHeader(svgWidth, svgHeight, boundingBox) {
  const { width, height } = boundingBox;
  return `<?xml version="1.0" standalone="no"?>
<!DOCTYPE svg PUBLIC "-//W3C//DTD SVG 1.1//EN" "http://www.w3.org/Graphics/SVG/1.1/DTD/svg11.dtd">
<svg tabindex="1" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width='${svgWidth}' height='${svgHeight}' viewBox='0 0 ${width} ${height}'>
<defs xmlns="http://www.w3.org/1999/xhtml"><style type="text/css">
@font-face {
  font-family: 'Abel';
  font-style: normal;
  font-weight: 400;
  src: local('Abel'), local('Abel-Regular'), url('data:font/woff2;base64,') format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2212, U+2215, U+E0FF, U+EFFD, U+F000;
}
</style></defs>`;
}



